<template>
  <div class="back-top" title="回到顶部" @click="backTop" v-if="btnVisible">
    <div class="arrow-open"></div>
  </div>
</template>
<script>
  export default {
    name: 'BackToTop',
    props: {
      scrollSign: String // scroll sign
    },
    data () {
      return {
        appElement: '', // exist scroll ele
        scrollTop: 0
      }
    },
    computed: {
      btnVisible () {
        return this.scrollTop > 0
      }
    },
    created () {
    },
    mounted () {
      this.$nextTick(() => {
        let scrollEle = this.$refs[this.scrollSign] || document.getElementById(this.scrollSign)
        this.scrollElement = scrollEle
        scrollEle.addEventListener('scroll', () => {
          this.scrollTop = scrollEle.scrollTop
        }, false)
      })
    },
    methods: {
      backTop () {
        let interval = setInterval(() => {
          if (this.scrollTop < 50) {
            this.scrollElement.scrollTo(0, 0)
            clearInterval(interval)
          } else {
            this.scrollTop -= 50
            this.scrollElement.scrollTo(0, this.scrollTop)
          }
        }, 10)
      }
    }
  }
</script>
<style scoped lang="scss">
  .back-top {
    background: #bbbdc1;
    width: 45px;
    height: 50px;
    opacity: .7;
    position: fixed;
    bottom: 45px;
    right: 45px;
    z-index: 2000;
    cursor: pointer;
    >div {
      width: 12px;
      height: 12px;
      border-top: 3px solid #fff;
      border-left: 3px solid #fff;
      -webkit-transform: rotate(45deg);
      transform: rotate(45deg);
      margin: 20px auto;
    }
  }
  .back-top:hover {
    opacity: .6;
  }

</style>
